<template>
    <div class="Echarts">
        <div id="main"></div>
    </div>
</template>

<script>
export default {
    name: "Echarts",
    data() {
        return {
            // echartdata: [
            //     { value: 1048, name: "Search Engine22" },
            //     { value: 735, name: "Direct" },
            //     { value: 580, name: "Email" },
            //     { value: 484, name: "Union Ads" },
            //     { value: 300, name: "Video Ads" },
            // ],
        };
    },
    props: ["echartdata"],
    methods: {
        resetEcharts() {
            let myChart = this.$echarts.init(document.getElementById("main"));
            if (myChart != null && myChart != "" && myChart != undefined) {
                myChart.dispose(); //销毁
            }
        },
        myEcharts() {
            var myChart = this.$echarts.init(document.getElementById("main"));

            //配置图表
            var option = {
                title: {
                    text: "Referer of a Website",
                    subtext: "Fake Data",
                    left: "center",
                },
                tooltip: {
                    trigger: "item",
                },
                legend: {
                    orient: "vertical",
                    left: "left",
                },
                series: [
                    {
                        name: "Access From",
                        type: "pie",
                        radius: "50%",
                        data: this.echartdata,
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: "rgba(0, 0, 0, 0.5)",
                            },
                        },
                    },
                ],
            };

            myChart.setOption(option, true);
        },
    },
    mounted() {
        // this.myEcharts();
    },
};
</script>

<style scoped>
#main {
    max-width: 600px;
    height: 400px;
}
</style>